.feature-image-banner {
	height: 500px;
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	background-color: rgba(0, 0, 0, 0.3);

	@media(max-width: 768px) {
		height: 380px;
	}

	&::before {
		content: '';
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		// background: rgba(0,0,0,0.5);
	}

	.single-post-header {
		position: relative;
		z-index: 2;

		.single-post-title {
			font-size: 48px;
			font-weight: 600;
			color: #fff;
			margin-bottom: 0;
			line-height: 1.3;
			-ms-word-wrap: break-word;
			word-wrap: break-word;

			@media(max-width: 768px) {
				font-size: 34px;
			}

			@media(max-width: 480px) {
				font-size: 30px;
			}
		}

		.post-meta {
			margin-top: 24px;

			li, a {
				color: #fff;
			}

			a {
				&:hover {
					color: $color__theme;
				}
			}
		}
	}
}

.blog-share {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-top: 20px;

	.share-title p {
		color: #2b2350;
		display: inline-block;
		margin: 0;
		font-weight: 500;
		font-size: 20px;
	}
}

.share-link {
	margin: 0;
	padding: 0;
	list-style: none;

	li {
		display: inline-block;
		margin-right: 5px;
		padding-left: 0 !important;

		&:before {
			display: none;
		}

		&:last-child {
			margin-right: 0;
		}

		a {
			display: block;
			height: 44px;
			width: 44px;
			text-align: center;
			line-height: 43px;
			color: #aba8a4;
			border-radius: 50%;
			font-size: 14px;
			border: 1px solid #e5dada;

			&:hover {
				background: $color__theme;
				color: #fff;
				border-color: $color__theme;
