}

@media (max-width: 768px) {

	.post-wrapper {
		&.blog-masonry {
			&.column-3 {
				.post-item {
					width: 100%;
					margin-right: 0;
				}
			}
		}
	}

	.comment-list {
		.comment {
			.comment-body {
				&.menu-comments {
					.comment-author {
						height: 60px;
						width: 60px;
						margin-right: 15px;
					}
				}

				.comment-author {
					height: 60px;
					width: 60px;
					margin-right: 15px;
				}

				.comment-content {
					padding-bottom: 20px;

