.page-links {
	clear: both;
	margin: 0 0 1.5em;
	padding-top: 20px;

	a,
	.current {
		height: 30px;
		width: 30px;
		border-radius: 50%;
		background: #f8f6f2;
		color: $color__heading;
		display: inline-block;
		text-align: center;
		line-height: 31px;
		font-size: 16px;
		margin-left: 5px;

		&:hover {
			background: $color__theme;
			color: #fff;
		}
	}

	.current {
		background: $color__theme;
		color: #fff;
	}
}


.post-navigation {
	margin-bottom: 50px;

	.nav-links {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		border-top: 1px solid #eee;
		border-bottom: 1px solid #eee;
		position: relative;
		width: calc(100% + 2%);

		.nav-previous,
		.nav-next {
			width: calc(50% - 2%);
			height: 50px;
			line-height: 50px;
			padding: 0 15px;
			background: #333;
			margin-right: 2%;
			flex: 1;
			font-weight: 500;
			text-align: center;
			transition: all 0.3s ease-in-out;

			a {
				font-weight: 500;
				font-size: 16px;
				color: #fff;
				display: block;
				outline: 0;

				&:hover {
					color: #fff;
				}
			}

			&:hover {
				background: $color__theme;
			}
		}

		.nav-previous {
			padding-right: 30px;

		}

		.nav-next {
			padding-left: 30px;
		}
	}
}


.first-lg {
	-webkit-order: -1;
	-ms-flex-order: -1;
	-webkit-box-ordinal-group: 0;
	order: -1;
}


