.page-header, .event-page-header {
	min-height: 350px;
	padding: 50px 0;
	height: auto;
	display: flex;
	align-items: center;
	position: relative;
	overflow: hidden;
	background-size: cover;
	background-position: center center;
	background: $color__gredient;

	//&:after {
	//	content: '';
	//	position: absolute;
	//	width: 100%;
	//	height: 100%;
	//	left: 0;
	//	top: 0;
	//	background: rgba(0,0,0,0.5);
	//}

	@media(max-width: 991px) {
		min-height: 300px !important;
	}

	.page-header_wrapper {
		position: relative;
		z-index: 2;
		text-align: center;
	}

	.page-header_title {
		color: $color__heading-2;
		font-size: 70px;
		font-weight: 700;
		position: relative;
		z-index: 2;
		line-height: 1.2;
		margin-bottom: 10px;

		@media #{$tab}{
			font-size: 40px !important;
		}
		@media #{$mobile}{
			font-size: 34px !important;
		}
	}

	.breadcrumbs {
		font-size: 18px;
		font-weight: 400;
		color: $color__body;

		a {
			color: $color__heading-2;

			&:hover {
				color: $color__theme;
			}
		}

		.separator {
			display: inline-block;
			margin: 0 9px;
			height: 4px;
			width: 4px;
			border-radius: 20px;
			background-color: $color__body;
		}
	}

	&.single-post-header-bg {
		background-color: #292a7f;
		min-height: 500px;
		background-size: cover;
		background-position: center center;
		text-align: left;

		.single-post-title {
			max-width: 700px;
			font-size: 40px;
			line-height: 50px;
			text-align: left;
			margin: 0  0 23px;
		}

		.feature-image {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;

			img {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		.overlay-bg {
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background-color: rgba(0,0,0,0.5);
			z-index: 1;
		}

		.post-meta {
			li {
				color: #fff;
				a {
					color: #fff;
				}
			}
		}
	}

}

.event-page-header {
	min-height: 350px;
}

.product-page-header {
	&:not(.shop-single-breadcrumb) {
		padding: 60px 0;
		background-color: #fafafa;
		min-height: 200px;
		display: flex;
		align-items: center;
	}

	.page-header_title {
		position: relative;
		font-size: 50px;
		line-height: 1.2;
		font-weight: 700;
		margin-bottom: 10px;
		z-index: 2;
	}
}

.shop-single-breadcrumb {
	padding: 30px 0;
	background: #fafafc;
}


.header_transparent ~ .page-header .page-header_wrapper,
.header_transparent ~ .event-page-header .page-header_wrapper {
	margin-top: 120px;

	@media(max-width: 991px) {
		margin-top: 50px;
	}
}

